:root {
    --wine-red: 220, 38, 38;
    --wine-burgundy: 136, 19, 55;
    --wine-gold: 251, 191, 36;
    --wine-white: 254, 243, 199;
    --glass-blur: 20px;
    --glass-opacity: 0.1;
    --animation-duration: 0.3s;
    --stagger-delay: 0.05s;
}

* { 
    box-sizing: border-box; 
    margin: 0; 
    padding: 0; 
}

html { 
    scroll-behavior: smooth;
    scrollbar-width: thin;
    scrollbar-color: rgb(var(--wine-burgundy)) #0a0a0a;
}

body { 
    font-family: 'Georgia', 'Times New Roman', serif; 
    background: #000;
    color: #fff;
    overflow-x: hidden;
    position: relative;
}

/* Modern Scrollbar */
::-webkit-scrollbar { width: 8px; height: 8px; }
::-webkit-scrollbar-track { background: #0a0a0a; }
::-webkit-scrollbar-thumb { 
    background: linear-gradient(180deg, rgb(var(--wine-red)), rgb(var(--wine-burgundy)));
    border-radius: 10px;
}

/* Animated Gradient Background */
.animated-gradient-bg {
    position: fixed;
    inset: 0;
    background: 
        radial-gradient(circle at 20% 50%, rgba(var(--wine-red), 0.1) 0%, transparent 50%),
        radial-gradient(circle at 80% 80%, rgba(var(--wine-burgundy), 0.1) 0%, transparent 50%),
        radial-gradient(circle at 40% 20%, rgba(var(--wine-gold), 0.05) 0%, transparent 50%),
        #000;
    z-index: -1;
}

/* Animated Orbs */
.floating-orb {
    position: absolute;
    border-radius: 50%;
    filter: blur(60px);
    opacity: 0.3;
    animation: float 20s infinite ease-in-out;
}

@keyframes float {
    0%, 100% { transform: translate(0, 0) scale(1); }
    33% { transform: translate(30px, -30px) scale(1.1); }
    66% { transform: translate(-20px, 20px) scale(0.9); }
}

/* Glassmorphism Base */
.glass {
    background: rgba(255, 255, 255, var(--glass-opacity));
    backdrop-filter: blur(var(--glass-blur));
    -webkit-backdrop-filter: blur(var(--glass-blur));
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.glass-dark {
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(var(--glass-blur));
    -webkit-backdrop-filter: blur(var(--glass-blur));
    border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Modern Card Styles */
.wine-card-modern {
    position: relative;
    background: linear-gradient(135deg, 
        rgba(255, 255, 255, 0.1) 0%, 
        rgba(255, 255, 255, 0.05) 100%);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 24px;
    overflow: hidden;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
    transform-style: preserve-3d;
    transform: perspective(1000px) rotateX(0deg) rotateY(0deg);
}

.wine-card-modern::before {
    content: '';
    position: absolute;
    inset: 0;
    background: radial-gradient(
        circle at var(--mouse-x, 50%) var(--mouse-y, 50%),
        rgba(var(--wine-gold), 0.15) 0%,
        transparent 50%
    );
    opacity: 0;
    transition: opacity 0.3s;
    pointer-events: none;
}

.wine-card-modern:hover::before {
    opacity: 1;
}

.wine-card-modern:hover {
    transform: perspective(1000px) rotateX(var(--rotate-x, 0deg)) rotateY(var(--rotate-y, 0deg)) scale(1.02);
    border-color: rgba(var(--wine-gold), 0.5);
    box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.3),
        0 0 60px rgba(var(--wine-red), 0.1),
        inset 0 0 30px rgba(255, 255, 255, 0.05);
}

/* Condensed Card Styles */
.wine-card-condensed {
    position: relative;
    background: linear-gradient(135deg, 
        rgba(255, 255, 255, 0.08) 0%, 
        rgba(255, 255, 255, 0.03) 100%);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    padding: 1rem;
    transition: all 0.3s cubic-bezier(0.23, 1, 0.320, 1);
}

.wine-card-condensed:hover {
    background: linear-gradient(135deg, 
        rgba(255, 255, 255, 0.12) 0%, 
        rgba(255, 255, 255, 0.06) 100%);
    border-color: rgba(var(--wine-gold), 0.3);
    transform: translateX(8px);
}

/* Glow Effects */
.glow-gold {
    box-shadow: 
        0 0 20px rgba(var(--wine-gold), 0.5),
        0 0 40px rgba(var(--wine-gold), 0.3),
        0 0 60px rgba(var(--wine-gold), 0.1);
}

.glow-text {
    text-shadow: 
        0 0 10px rgba(var(--wine-gold), 0.5),
        0 0 20px rgba(var(--wine-gold), 0.3),
        0 0 30px rgba(var(--wine-gold), 0.1);
}

/* Modern Button */
.btn-modern {
    position: relative;
    padding: 12px 32px;
    background: linear-gradient(135deg, rgb(var(--wine-red)), rgb(var(--wine-burgundy)));
    border: none;
    border-radius: 50px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    overflow: hidden;
    transition: all 0.3s;
}

.btn-modern::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: width 0.6s, height 0.6s;
}

.btn-modern:hover::before {
    width: 300px;
    height: 300px;
}

.btn-modern:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 30px rgba(var(--wine-red), 0.3);
}

/* Hero Video Section */
.hero-modern {
    position: relative;
    height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    z-index: -1;
}

.hero-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.3) 0%,
        rgba(0, 0, 0, 0.5) 50%,
        rgba(0, 0, 0, 0.8) 100%
    );
    z-index: 1;
}

/* Stagger Animation */
.stagger-in {
    opacity: 0;
    transform: translateY(30px);
    animation: staggerIn 0.8s cubic-bezier(0.23, 1, 0.320, 1) forwards;
}

@keyframes staggerIn {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Loading Animation */
.wine-loader {
    position: relative;
    width: 60px;
    height: 60px;
}

.wine-loader::before,
.wine-loader::after {
    content: '';
    position: absolute;
    inset: 0;
    border: 3px solid transparent;
    border-top-color: rgb(var(--wine-red));
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

.wine-loader::after {
    inset: 8px;
    border-top-color: rgb(var(--wine-gold));
    animation-duration: 0.7s;
    animation-direction: reverse;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

/* Navbar Modern */
.navbar-modern {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    transition: all 0.3s;
    padding: 1rem 0;
}

.navbar-modern.scrolled {
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 0.5rem 0;
}

/* Wine Bottle 3D Container */
.bottle-3d-container {
    position: relative;
    width: 100%;
    height: 300px;
    cursor: grab;
}

.bottle-3d-container:active {
    cursor: grabbing;
}

/* Modern Filter Pills */
.filter-pill {
    padding: 8px 20px;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: white;
    font-size: 14px;
    transition: all 0.3s;
    cursor: pointer;
    backdrop-filter: blur(10px);
}

.filter-pill:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateY(-2px);
}

.filter-pill.active {
    background: linear-gradient(135deg, rgb(var(--wine-red)), rgb(var(--wine-burgundy)));
    border-color: transparent;
}

/* AI Assistant Styles */
.ai-assistant-btn {
    position: fixed;
    bottom: 24px;
    right: 24px;
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, rgb(var(--wine-red)), rgb(var(--wine-burgundy)));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
    transition: all 0.3s;
    z-index: 99;
}

.ai-assistant-btn:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 30px rgba(var(--wine-red), 0.4);
}

.ai-chat-window {
    position: fixed;
    bottom: 100px;
    right: 24px;
    width: 380px;
    height: 500px;
    background: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    z-index: 100;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.ai-chat-header {
    background: linear-gradient(135deg, rgba(var(--wine-red), 0.2), rgba(var(--wine-burgundy), 0.2));
    padding: 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.ai-chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.ai-message {
    max-width: 80%;
    padding: 0.75rem 1rem;
    border-radius: 18px;
    word-wrap: break-word;
}

.ai-message.user {
    align-self: flex-end;
    background: linear-gradient(135deg, rgb(var(--wine-red)), rgb(var(--wine-burgundy)));
    color: white;
}

.ai-message.assistant {
    align-self: flex-start;
    background: rgba(255, 255, 255, 0.1);
    color: white;
}

.ai-chat-input {
    padding: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.ai-chat-input input {
    width: 100%;
    padding: 0.75rem 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 25px;
    color: white;
    outline: none;
    transition: all 0.3s;
}

.ai-chat-input input:focus {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(var(--wine-red), 0.5);
}

/* Responsive */
@media (max-width: 768px) {
    .hero-modern {
        height: 80vh;
    }
    
    .wine-card-modern {
        border-radius: 16px;
    }

    .ai-chat-window {
        width: calc(100vw - 48px);
        right: 24px;
        left: 24px;
    }
}

/* Smooth Reveal Animation */
.reveal {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.8s cubic-bezier(0.23, 1, 0.320, 1);
}

.reveal.active {
    opacity: 1;
    transform: translateY(0);
}

/* Wine Score Badge Modern */
.score-badge {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: conic-gradient(
        from 0deg,
        rgb(var(--wine-gold)) 0deg,
        rgb(var(--wine-red)) calc(var(--score) * 3.6deg),
        rgba(255, 255, 255, 0.1) calc(var(--score) * 3.6deg)
    );
    animation: rotateIn 1s cubic-bezier(0.23, 1, 0.320, 1);
}

@keyframes rotateIn {
    from { transform: rotate(-90deg) scale(0); }
    to { transform: rotate(0deg) scale(1); }
}

.score-badge::before {
    content: '';
    position: absolute;
    inset: 4px;
    background: #000;
    border-radius: 50%;
    z-index: 0;
}

.score-badge span {
    position: relative;
    z-index: 1;
    font-weight: bold;
    font-size: 18px;
}

/* Footer Styles */
footer {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.9));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    margin-top: 5rem;
}